import * as React from "react"
import {Grid} from "react-virtualized";
import {inject, observer} from "mobx-react";
import {GameTile} from "./GameTile";
import {getKey} from "./utils/utils";
import {MIN_BOARD_HEIGHT, MIN_BOARD_WIDTH, MIN_TILE_SIZE, TILE_TYPE} from "../consts";

@inject('GameStore')
@observer
class GameBoard extends React.Component {

    computeCell = (i, j) => {
        const store = this.props.GameStore
        if(store.loading){
            return TILE_TYPE.UNCLICKED
        }
        const key = getKey(i, j)

        const clicked = store.revealBoard || store.clickedCells[key]

        if (store.isAMine(i, j) && clicked) {
            return TILE_TYPE.REVEALED_MINE
        }
        if (store.flaggedCells[key] && !store.revealBoard) {
            return TILE_TYPE.FLAG
        }
        return clicked ? TILE_TYPE.CLICKED : TILE_TYPE.UNCLICKED

    }

    cellRenderer = ({columnIndex, key, rowIndex, style}) => {
        const {GameStore} = this.props
        const cellType = this.computeCell(rowIndex, columnIndex) //GameStore.board[rowIndex][columnIndex]
        const adj = cellType === TILE_TYPE.CLICKED ? GameStore.calculateAdjMines(rowIndex, columnIndex) : -1;
        const colorType = (rowIndex + columnIndex) % 2
        return <div className="tile" id={`${rowIndex}-${columnIndex}`} key={key} style={style}>
            <GameTile colorType={colorType} adj={adj} cellState={cellType}/>
        </div>
    }

    render() {
        const store = this.props.GameStore
        const tableH = Math.min(store.height,20)
        const tableW = Math.min(store.width,20)
        const superman = store.superman
        console.log("render",)
        const TILE_SIZE_WIDTH = (MIN_BOARD_WIDTH / tableW) < MIN_TILE_SIZE ? MIN_TILE_SIZE :Math.floor( MIN_BOARD_WIDTH / tableW)
        const TILE_SIZE_HEIGHT = (MIN_BOARD_HEIGHT / tableW) < MIN_TILE_SIZE ? MIN_TILE_SIZE :Math.floor( MIN_BOARD_HEIGHT / tableW)
        const numClicks = store.numberOfClicks // this is for rerender
        console.log(TILE_SIZE_WIDTH,TILE_SIZE_HEIGHT)
        const TILE_SIZE=Math.max(TILE_SIZE_HEIGHT,TILE_SIZE_WIDTH)
        return <Grid cellRenderer={this.cellRenderer}
                     columnCount={store.width}
                     style={{outline: 'none',padding:"0 18px 18px 0"}}
                     rowCount={store.height}
                     columnWidth={TILE_SIZE}
                     height={tableH * TILE_SIZE}
                     width={tableW * TILE_SIZE}
                     rowHeight={TILE_SIZE}
                     clicks={numClicks}
                     superman={superman}
        >
        </Grid>
    }
}

export default GameBoard