import * as React from "react"
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';


export const Superman=(props)=>{
    return  <div style={{display:"flex",alignItems:"center",paddingLeft:"10px"}}>
    <FormControlLabel
        control={
            <Checkbox onChange={props.toggle} icon={<FavoriteBorder />} checkedIcon={<Favorite />} value={props.value} />
        }
    />
    <div style={{position:"relative",right:"20px"}}>Superman</div>
    </div>
}