import * as React from "react"
import TextField from "@material-ui/core/TextField";
import Fab from "@material-ui/core/Fab";
import Button from "@material-ui/core/Button";

export const GameSettings = (props) => {

    function handleKeyPress(ev){
        console.log("key",ev)
        if(ev.key==="Enter"){
            props.handleNewGame()
        }

    }
    return <div id='b' style={{display:"flex",alignItems:"flex-end",flexDirection:"column"}} onKeyPress={handleKeyPress}>
        <TextField
            id="gameWidth"
            label="Columns"
            placeholder="6"
            type="number"
            multiline
            margin="normal"
            // variant="outlined"
            value={props.gameWidth}
            onChange={props.handleInput}
        />
        <TextField
            label="Rows"
            placeholder="6"
            type="number" id="gameHeight"
            multiline
            margin="normal"
            value={props.gameHeight}
            onChange={props.handleInput}
        />
        <TextField
            label="Mines"
            placeholder="6"
            multiline
            margin="normal"
            type="number"
            id="mines"
            onChange={props.handleInput}
            value={props.mines}
        />


    </div>
}