import * as React from "react"
import {inject, observer} from "mobx-react";
import GameBoard from "./GameBoard";
import {GameSettings} from "./GameSettings";
import {FlagsLeft} from "./FlagsLeft";
import AlertDialog from "./assets/AlertDialog";
import CircularProgressbar from "react-circular-progressbar";
import {withStyles} from "@material-ui/core";
import {Superman} from "./SupermanCheckbox";
import LinearProgress from '@material-ui/core/LinearProgress';
import Button from "@material-ui/core/Button";


const styles = theme => ({

    root: {
        display: "grid",
        padding: "10px",
        margin: "10px",
        gridTemplateColumns: "minmax(80px,min-content) min-content",
    }
});

@inject('GameStore')
@withStyles(styles)
@observer
class MineSweeperGame extends React.Component {
    state = {
        gameHeight: 6,
        gameWidth: 6,
        mines: 2,
    }

    startGame = () => { // needs to be from container
        const {GameStore} = this.props;
        GameStore.newGame(this.state.gameWidth, this.state.gameHeight, this.state.mines)
    }

    componentDidMount() {
        this.startGame();
    }

    handleNewGame = () => {
        // ev.preventDefault();
        this.startGame()
    }


    handleTileClick = (ev) => {
        const tile = ev.target.closest('.tile')
        if (!tile || this.props.GameStore.loading) {
            return
        }
        const [rowIndex, columnIndex] = tile.id.split("-");
        if (!rowIndex || !columnIndex) {
            return
        }
        // if (ev.shiftKey) {
        //     document.getSelection().removeAllRanges();
        // }
        this.props.GameStore.handleClickTile(parseInt(rowIndex), parseInt(columnIndex), ev.shiftKey)
    }

    handleInput = (ev) => {
        const val = parseInt(ev.target.value)
        this.setState({[ev.target.id]: val ? val : ''});
    }

    render() {
        console.log("game")
        const {GameStore} = this.props;
        const won = GameStore.won
        console.log(won)
        const notValidDialog =
            <AlertDialog
                open={GameStore.wrongParams}
                handleOk={GameStore.closeParamsDialog}
                title={"Oopsie!"}
                text="Valid Field size:  1-300 :Valid Mines Number: 1- Game Board Size   "
                handleClose={GameStore.closeParamsDialog}/>
        const wonDialog =
            <AlertDialog open={GameStore.won} handleOk={this.startGame} text="Can you do it again?" title={"Woohoo!"}/>
        const lostDialog =
            <AlertDialog open={GameStore.lost} handleOk={this.startGame} text="Let's try again!" title={"Boom :("}/>
        const noFlagsDialog =
            <AlertDialog title="Out of flags"
                         handleOk={GameStore.hideWarning}
                         open={GameStore.displayWaring} handleClose={GameStore.hideWarning}/>
        return <div className={this.props.classes.root} onClick={this.handleTileClick}>
            <div style={{gridColumn: "1", gridRow: "2/3", background: "#b648f2", padding: "12px"}}>

                <GameSettings handleNewGame={this.handleNewGame}
                              handleInput={this.handleInput} gameHeight={this.state.gameHeight}
                              gameWidth={this.state.gameWidth}
                              mines={this.state.mines}/>
            </div>
            <div style={{
                color: "white",
                gridRow: "1",
                gridColumn: "2/4",
                alignitems: "center",
                display: "flex",
                justifyContent: "space-between",
                background: "#4a752c"
            }}>
                <Superman toggle={GameStore.toggleSuperman}/>
                <FlagsLeft flags={GameStore.minesNumber - GameStore.flags}/>

            </div>
            <div style={{gridRow: "1", gridColumn: "1", background: "#ffcd1a"}}>
                <Button style={{height: "100%", width: "100%", color: "white"}} color="inherit"
                        onClick={this.handleNewGame}>
                    Go!
                </Button>
            </div>
            <div style={{gridRow: "2/3 ", gridColumn: "2"}}>
                {GameStore.loading && <LinearProgress/>}
                <GameBoard/>
            </div>
            {wonDialog}
            {lostDialog}
            {noFlagsDialog}
            {notValidDialog}

        </div>
    }
}


export default MineSweeperGame